import React from 'react';
import ReactEditableInput from '@garphild/react-editable-input';
class UDField extends React.Component {

  // state is for keeping control state before or after changes.
  state = {
    editMode: false,
    value: this.props.value,
    hidden: false
  };
  componentWillMount() {
    this.pubSubToken = PubSub.subscribe(this.props.id, this.onIncomingEvent.bind(this));
  }

  componentWillUnmount() {
    PubSub.unsubscribe(this.pubSubToken);
  }

  onIncomingEvent(eventName, event) {
    if (event.type === "requestState") {
      var data = {
        attributes: {
          value: this.state.value,
          hidden: this.state.hidden,
        }
      }
      UniversalDashboard.post(`/api/internal/component/element/sessionState/${event.requestId}`, data);
    }
    else if (event.type === "setState") {
      this.setState(event.state.attributes);
    }
    else if (event.type === "clearElement") {
      this.setState({
        value: ''
      });
    }
    else if (event.type === "removeElement") {
      this.setState({
        hidden: true
      });
    }
  }
  toggleEditMode = () => {
    const { editMode } = this.state;
    this.setState({
      editMode: true,
    });
  };

  onEditEnd = () => {
    this.setState({
      editMode: false,
    });
  };

  onEditChange = (e) => {
    this.setState({
      value: e.currentTarget.value,
    });
    if (this.props.activeOnChange) {
      UniversalDashboard.publish("element-event", {
        type: "clientEvent",
        eventId: this.props.id + "onChange",
        eventName: "onEditChange",
        eventData: e.value
      });
    }
  };

  render() {
    if (this.state.hidden) {
      return null;
    }
    const {
      editMode,
      value,
    } = this.state;
    return (
      <>
        <ReactEditableInput
          value={value}
          toggleEditMode={this.toggleEditMode}
          editMode={editMode}
          changeListener={this.onEditChange}
          onEditEnd={this.onEditEnd}
          disabled={false}
          fullwidth={true}
          label={this.props.label}
        />
      </>
    );
  }
}

export default UDField
